@charset "UTF-8";
:root {
  --white: #fff;
  --black: #000;
  --main: #FF0000;
  --dark: #0d0d0d;
  --darkBlue: #0e1035;
}

:root {
  --font-family: "Montserrat", sans-serif;
  --font-family2: Arial, sans-serif;
  --font-family3: "Roboto", Arial, sans-serif;
  --light: 300;
  --regular: 400;
  --medium: 500;
  --semibold: 600;
  --bold: 700;
  --extra-bold: 800;
}

:root {
  --base-transition: all .34s ease-out;
}

/*==========  RESET STYLES  ==========*/
html {
  overflow-y: scroll;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  -webkit-text-size-adjust: none;
  box-sizing: border-box;
}

html:not(.touch-screen)::-webkit-scrollbar,
html:not(.touch-screen) *::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

html:not(.touch-screen)::-webkit-scrollbar-track,
html:not(.touch-screen) *::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.08);
  border-radius: 8px;
}

html:not(.touch-screen)::-webkit-scrollbar-thumb,
html:not(.touch-screen) *::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.08);
}

/*==========  LOADER  ==========*/
#loader-wrapper {
  position: fixed;
  left: 0;
  top: -100px;
  right: 0;
  bottom: -100px;
  background: #ffffff;
  z-index: 2000;
}

/*-------------------------------------------------------------------------------------------------------------------------------*/
/* GLOBAL SETTINGS */
/*-------------------------------------------------------------------------------------------------------------------------------*/
html {
  height: 100%;
}

body {
  height: 100%;
}

#content-block {
  overflow: hidden;
  position: relative;
}

.overflow-hidden {
  overflow: hidden;
}

.position-relative {
  position: relative;
}

html.overflow-hidden body {
  overflow: hidden;
}

/*-------------------------------------------------------------------------------------------------------------------------------*/
/* ANIMATIONS */
/*-------------------------------------------------------------------------------------------------------------------------------*/
.simple-animation {
  transform: translate3d(0, -50px, 0);
  opacity: 0;
  transition: all 0.5s ease-out;
}

.bottom-animation {
  transform: translate3d(0, 50px, 0);
  opacity: 0;
  transition: all 0.5s ease-out;
}

.simple-animation.animated,
.bottom-animation.animated {
  transform: none;
  opacity: 1;
}

.delay-01 {
  transition-delay: 0.1s;
}

.delay-02 {
  transition-delay: 0.2s;
}

.delay-03 {
  transition-delay: 0.3s;
}

.delay-04 {
  transition-delay: 0.4s;
}

.delay-05 {
  transition-delay: 5s;
}

.delay-06 {
  transition-delay: 6s;
}

.delay-07 {
  transition-delay: 1.4s;
}

