/* BG */
.video-bg:after,
.bg:after {
  content: "";
  display: block;
}

.video-bg,
.video-bg:after,
.wr-hd,
.bg,
.bg:after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.bg {
  background-size: cover;
  background-repeat: no-repeat;
}

.wr-hd {
  overflow: hidden;
  z-index: -1;
}

.video-bg video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%) scale(1.1);
}

/* Icon arrow */
.icon-arrow {
  position: relative;
  display: inline-block;
  width: 7px;
  height: 7px;
}

.icon-arrow:before, .icon-arrow:after {
  content: "";
  position: absolute;
  top: 0;
  width: 1px;
  height: 9px;
  background-color: #111111;
}

.icon-arrow:before {
  left: 0;
  transform: rotate(-45deg);
}

.icon-arrow:after {
  right: 0;
  transform: rotate(45deg);
}

.image-wrapper img {
  max-width: 100%;
}

.image-wrapper.st0 {
  border-radius: 25px;
  border: 2px solid #e80000;
  box-shadow: 0px 0px 15px 2px rgba(255, 0, 0, 0.2);
}

.image-wrapper.st0 img {
  border-radius: 25px;
}

/*==========  GLOBAL  ==========*/
body {
  font-family: var(--font-family);
  color: var(--dark);
}

b, strong {
  font-weight: 700;
}

a {
  transition: var(--base-transition);
}

a:hover {
  text-decoration: none;
}

ul, ol {
  list-style: none;
}

/*==========  SIZE  ==========*/
/* Text size */
body, .text {
  line-height: 1.65em;
  font-size: 16px;
  font-weight: var(--regular);
}

.text-md {
  line-height: 1.3em;
