}

.text-lg-0 {
  line-height: 1.5em;
  font-size: 20px;
}

.text-lg-1 {
  line-height: 1.5em;
  font-size: 25px;
}

.text-lg {
  line-height: 1.5em;
  font-size: 28px;
}

.text-xl {
  line-height: 1.3em;
  font-size: 24px;
}

h1, .h1 {
  line-height: 1.3em;
  font-size: 72px;
}

.h2-1 {
  line-height: 1.3em;
