/*==========  BUTTONS  ==========*/
.btn {
  display: inline-block;
  vertical-align: bottom;
  text-align: center;
  position: relative;
  cursor: pointer;
  text-decoration: none;
  border: none;
  padding: 0;
  background: none;
  outline: none;
}

.btn-primary {
  color: var(--white);
  font-size: 20px;
  line-height: 1.55;
  font-weight: 600;
  border-width: 3px;
  border-radius: 20px;
  background-color: var(--main);
  background-position: center center;
  border-color: transparent;
  border-style: solid;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border-color 0.2s ease-in-out;
  box-shadow: 4px 4px 10px 0px rgba(13, 13, 13, 0.2);
  padding: 9.5px 20px;
}

.btn-primary.xl {
  font-size: 25px;
  border-width: 4px;
  padding: 12px 20px;
}

.btn-primary.mx-560 {
