@keyframes slide-image-dotted-anim {
  0% {
    transform: rotate(0deg);
    animation-timing-function: linear;
  }
  100% {
    transform: rotate(90deg);
  }
}
.slide-image {
  padding: 10px;
  position: relative;
  width: max-content;
  margin-left: auto;
}

.slide-image:after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  max-width: 360px;
  max-height: 360px;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  border: dotted 4px #ff0000;
  animation: slide-image-dotted-anim 10s infinite linear;
  backface-visibility: hidden;
  opacity: 0.5;
}

.slide-image__inner {
  border-radius: 50%;
  overflow: hidden;
  width: 340px;
  height: 340px;
}

.slide-image__item {
  width: 340px;
  height: 340px;
  border-radius: 50%;
  overflow: hidden;
  position: relative;
}

.slide-image .swiper-pagination {
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
}

.swiper-entry.st-default .swiper-pagination-bullet {
